<template>
  <li :class="hasChildren ? 'mb-2' : ''">
    <template v-if="node.title">
      <div @click="toggle" v-if="hasChildren" class="mb-2"><i :class="['far w-2 text-center', expandIcon]"></i> <span class="font-bold ml-2">{{ node.title }}</span></div>
      <vs-radio v-else v-model="mutableValue" :vs-value="node.id">{{ node.title }}</vs-radio>
    </template>
    <ul class="nv-tree" v-show="isExpanded" v-if="hasChildren">
      <nv-tree-node class="item" v-for="(childNode, index) in node.children" :key="index" :node="childNode" :value="value" v-on:nodeSelected="nodeSelected"></nv-tree-node>
    </ul>
  </li>
</template>

<script>
export default {
  name: 'nv-tree-node',
  props: {
    node: {
      type: Object,
      required: true
    },
    value: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      isExpanded: this.node.expanded || false,
      mutableValue: this.value
    }
  },
  computed: {
    hasChildren: function () {
      return this.node.children && this.node.children.length
    },
    expandIcon: function () {
      return this.isExpanded ? 'fa-caret-down' : 'fa-caret-right'
    }
  },
  methods: {
    toggle: function () {
      if (this.hasChildren) {
        this.isExpanded = !this.isExpanded
      }
    },
    nodeSelected: function (node) {
      this.$emit('nodeSelected', node)
    }
  },
  watch: {
    value: function (value) {
      this.mutableValue = value
    },
    mutableValue: function () {
      if (this.mutableValue === this.node.id) {
        this.$emit('nodeSelected', this.node)
      }
    }
  }
}
</script>
