<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BeneficiaryService from '@/services/beneficiary/BeneficiaryService'
import CountryLookup from '@/views/nucleus/country/CountryLookup'
import CurrencyLookup from '@/views/nucleus/currency/CurrencyLookup'
import BankLookup from '@/views/bank/bank/BankLookup'
import BranchLookup from '@/views/bank/branch/BranchLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'
import NvTree from '@/components/nv-tree/NvTree'
import MarkdownItVue from 'markdown-it-vue'

export default {
  name: 'beneficiary-create',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    CountryLookup,
    BankLookup,
    BranchLookup,
    CurrencyLookup,
    NvTree,
    MarkdownItVue
  },
  data: function () {
    return {
      beneficiaryTypes: [],
      beneficiaryType: {},
      beneficiaryName: '',
      nickname: '',
      accountNumber: '',
      iban: '',
      countryGeoId: '',
      countryGeoName: '',
      bankId: '',
      bankName: '',
      swiftCode: '',
      preferredCurrency: '',
      preferredCurrencyUomId: '',
      address1: '',
      district: '',
      city: '',
      province: '',
      postalCode: '',
      postOfficeBox: '',
      swiftSelected: false
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryService.beneficiaryTypeTree({
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.beneficiaryTypes = response.data[0].children || []
      })
    },
    selectBeneficiaryTypeNode (node) {
      this.beneficiaryType = node.data
      this.bankId = ''
      this.bankName = ''
      this.countryGeoId = ''
      this.countryGeoName = ''
    },
    selectCountryRecord (record) {
      this.swiftSelected = false
      this.countryGeoId = record.geoId
      this.countryGeoName = record.geoName
    },
    selectBankRecord (record) {
      this.swiftSelected = false
      this.bankId = record.bankId
      this.bankName = record.bankName
    },
    selectSwiftCodeRecord (record) {
      if (Object.keys(record).length === 0) {
        this.swiftSelected = false
        this.countryGeoId = record.countryGeoId || this.countryGeoId
        this.countryGeoName = record.countryName || this.countryGeoName
        this.bankId = record.bankId || this.bankId
        this.bankName = record.bankName || this.bankName
        this.swiftCode = record.swiftCode
        this.city = record.city
      } else {
        this.swiftSelected = true
        this.countryGeoId = record.countryGeoId
        this.countryGeoName = record.countryName
        this.bankId = record.bankId
        this.bankName = record.bankName
        this.swiftCode = record.swiftCode
        this.city = record.city
      }
    },
    selectPreferredCurrencyRecord (record) {
      this.preferredCurrencyUomId = record.uomId
      this.preferredCurrency = record.description
    },
    validateBeneficiaryTypeSelected () {
      return (this.beneficiaryType.typeId || null) !== null
    },
    createBeneficiary (nextAction) {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryService.createBeneficiary({
        typeId: this.beneficiaryType.typeId,
        nickname: this.nickname,
        beneficiaryName: this.beneficiaryName,
        accountNumber: this.accountNumber,
        iban: this.iban,
        swiftCode: this.swiftCode,
        preferredCurrencyId: this.preferredCurrencyUomId,
        address1: this.address1,
        district: this.district,
        city: this.city,
        province: this.province,
        countryGeoId: this.countryGeoId,
        postalCode: this.postalCode,
        postOfficeBox: this.postOfficeBox
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBeneficiary', params: { id: response.data.beneficiaryId } })
          } else {
            self.$router.push({ name: 'BrowseBeneficiaries' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
