<template>
  <nv-tree-node :node="rootNode" :value="mutableValue" v-on:nodeSelected="nodeSelected"></nv-tree-node>
</template>

<script>
import NvTreeNode from '@/components/nv-tree/NvTreeNode'

export default {
  name: 'nv-tree',
  props: {
    rootNode: {
      type: Object,
      required: true
    },
    value: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      mutableValue: this.value
    }
  },
  components: {
    NvTreeNode
  },
  methods: {
    nodeSelected: function (node) {
      this.mutableValue = node.id
      this.$emit('nodeSelected', node)
    }
  },
  watch: {
    value: function (value) {
      this.mutableValue = value
    }
  }
}
</script>
